html, body {
  margin: 0;
  padding: 0;
  font-family: ‘Open Sans’, sans-serif;
  font-size: 18px;
}

h1 {
  color: #111;
  font-family: ‘Open Sans Condensed’, sans-serif;
  font-size: 64px;
  font-weight: 700;
  line-height: 64px;
  margin: 0 0 0;
  padding: 20px 30px;
  text-align: center;
  text-transform: uppercase;
}

h2 {
  color: #111;
  font-family: ‘Open Sans Condensed’, sans-serif;
  font-size: 48px;
  font-weight: 700;
  line-height: 48px;
  margin: 0 0 24px;
  padding: 0 30px;
  text-align: center;
  text-transform: uppercase;
}

h3 {
  color: #111;
  font-family: ‘Open Sans Condensed’, sans-serif;
  font-size: 32px;
  font-weight: 700;
  line-height: 36px;
  margin: 0 0 24px;
  /*padding: 0 30px;*/
  text-align: center;
  /*text-transform: uppercase;*/
}

p {
  color: #111;
  font-family: ‘Open Sans’, sans-serif;
  font-size: 18px;
  line-height: 26px;
  margin: 0 0 13px;
}

a {
  color: #1c92f6;
  text-decoration: none;
  cursor: pointer;
}

a:hover {
  text-decoration: underline
}

button {
  border: none;
  outline: none;
  background: none;

  cursor: pointer;
}

img {
  max-width: 70vw;
}

@media screen and (max-width: 768px) {
  p {
    font-size: 16px;
    line-height: 22px;
    margin: 0 0 11px;
  }
  h1 {
    font-size: 32px;
    font-weight: 700;
    line-height: 36px;
    margin: 0 0 0;
    padding: 10px 10px;
  }
  h2 {
    font-size: 28px;
    font-weight: 700;
    line-height: 32px;
    margin: 0 0 0;
    padding: 8px 8px;
  }
  h3 {
    font-size: 24px;
    font-weight: 700;
    line-height: 28px;
    margin: 0 0 0;
    padding: 6px 6px;
  }

  li {
    font-size: 16px;
  }
}


